<template>
    <div class="work-score-container">
        <el-card style="min-height:600px;">
            
            <el-tabs v-model="activeName">
                <el-tab-pane label="" name="look">
                    <div style="width:900px">
                        <el-row>
                            <el-form ref="form" :model="form" label-width="100px">
                                <el-form-item label="">
                                    <el-col>{{form.activityName}}</el-col>
                                </el-form-item>

                                <el-form-item label="">
                                    <!-- <el-col>{{form.introduction}}</el-col> -->
                                    <div class="content" v-html="form.introduction"></div>
                                </el-form-item>

                                <el-form-item label="">
                                    <el-col><a :href="form.regForm"></a></el-col>
                                </el-form-item>

                                <el-form-item label="">
                                    <el-col v-for="(item,index) in form.annex" :key="index"><a :href="item.url">{{index+1}}</a><span class="remark">:{{item.remark}}</span></el-col>
                                </el-form-item>    
                            </el-form>
                        </el-row>
                    </div>
                </el-tab-pane>
            </el-tabs>

            <el-tabs v-model="activeDetail">
                <el-tab-pane label="" name="detail">
                    <div style="width:900px">
                        <el-row>
                            <el-form ref="form" :model="form2" label-width="100px">
                                <el-form-item label="">
                                    <el-input
                                    placeholder=""
                                    v-model="form2.scope"
                                    clearable
                                    style="width:300px;"
                                    size="small">
                                    </el-input>
                                </el-form-item>

                                <el-form-item label="">
                                    <el-input
                                    type="textarea"
                                    :rows="6"
                                    placeholder=""
                                    v-model="form2.introduction">
                                    </el-input>
                                </el-form-item>

                            </el-form>
                        </el-row>
                    </div>
                </el-tab-pane>
            </el-tabs>
            <el-row>
                <el-col :span="2" :offset="11">
                    <el-button type="primary" @click="goToScope"></el-button>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>
<script>
import api from "../../service/api";
import { axiosGet, getCookie } from "../../tools/tools";
import axios from "axios";
export default {
    data(){
        return{
            activeName:'look',
            activeDetail:'detail',
            form: {
                activityName: '',
                introduction:'',
                regForm:[],
                annex:[]
            },
            form2:{
                scope:'',//
                introduction:'',//
            },
            
        }
    },
    mounted(){

    },
    created(){
        let that=this;
        that.getWorkDetail();
    },
    methods:{
        async getWorkDetail(){
            let that=this;
            let params={};
            params.url=api.enroll;
            params.id=that.$route.query.id;
            params.inscore=1;
            params.expand="works";
            await this.axiosGet(params).then(res=>{
                that.form.activityName=res.works.title;
                that.form.introduction=res.works.content;
                that.form.annex=res.works.attachment.map(res=>{
                    return{
                        "remark":res.remark,
                        "url":res.url,
                    }
                })
                that.form.regForm=res.registration;
            }).catch(err=>{
                console.log(err);
            });
        },
        //
        goToScope(){
            this.open()
        },
        //
        open() {
            this.$confirm('', '', {
                confirmButtonText: '',
                type: 'warning',
                center: true,
                showCancelButton:false,
            }).then(() => {
                this.$message({
                    type: 'success',
                     message: ''
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        }
    }
}
</script>
<style lang="less" scoped>
.work-score-container{
    width:1180px;
    margin:auto;
    margin-top:20px;
}
.remark{
    font-size:14px;
    font-weight:400;
    color:rgba(153,153,153,1);
}
</style>
